import { onMounted, onUnmounted, ref, Ref, watch } from 'vue'
/*
    
    /
    document,
*/
// DOM(hooksetupdom)
export default function (goalElement: Ref, fn: (param?: any) => void, immediatelyOpen = false) {
    const isOpenListenOut = ref<boolean>(false) // 
    const handleClickOutside = function (event: MouseEvent) {
        // contains
        if (!goalElement.value.contains(event.target)) {
            console.log('')
            fn(goalElement)
        } else {
            // console.log('')
        }
    }
    const addEvent = () => {
        document.addEventListener('click', handleClickOutside)
    }
    const removeEvent = () => {
        document.removeEventListener('click', handleClickOutside)
    }

    watch(isOpenListenOut, (newValue) => {
        if (newValue) {
            addEvent()
        } else {
            removeEvent()
        }
    })

    onMounted(() => {
        if (immediatelyOpen) {
            addEvent()
        }
    })
    onUnmounted(() => {
        removeEvent()
    })

    return {
        isOpenListenOut
    }
}
